<template>
  <div id="formulario-nota">
    <b-card
      border-variant="primary"
      :header="edit?'Editar una nota':'Agrega una nota'"
      header-bg-variant="primary"
      header-text-variant="white"
      align="center"
    >
      <b-card-body class="p-0">
        <div @submit="onSubmit">
            <b-form-input
                v-model="form.nombre"
                type="text"
                required
                placeholder="Nombre"
                class="mb-3"
            ></b-form-input>
            <b-form-textarea
                v-model="form.descripcion"
                placeholder="Descripcion"
                rows="3"
                no-resize
            ></b-form-textarea>
            <b-form-checkbox v-if="edit && typeof form.activo != null" v-model="form.activo" class="text-left mt-1" switch>
                <b>{{ form.activo ? 'Activo' : 'Inactivo'}}</b>
            </b-form-checkbox>

            <!-- Buttons -->
            <div class="d-flex mt-2">
                <b-button type="submit" block variant="success" @click.prevent="onSubmit" class="mr-1" v-text="edit?'Actualizar':'Crear'"></b-button>
                <b-button type="button" block variant="outline-danger" @click.prevent="onReset" class="ml-1 mt-0" v-text="edit?'Cancelar':'Resetear'"></b-button>
            </div> 
        </div>
      </b-card-body>
    </b-card>
  </div>
</template>

<script>
export default {
  props: ['notaEdit'],
  data() {
    return {
      form: {
        _id: null,
        nombre: "",
        descripcion: "",
        activo: null
      },
      edit: false,
    };
  },
  methods: {
    onSubmit() {
      let $this = this

      if(this.form.nombre.length >= 4 && this.form.descripcion.length >= 10){
          let action = '',
          data = {'msg':'','color':'success'}

          if(this.edit){
              action = 'editar'
              data.msg = 'Nota actualizada correctamente.'
          }else{
              action = 'create'
              data.msg = 'Nota creada correctamente.'
          }

          $this.$store.dispatch(action,this.form)
          .then(
            res => {
              $this.$store.dispatch('alerta',data)
              $this.onReset()
            },
            err => {
              data.msg = 'Error al ejecutar la accion'
              data.color = 'danger'
              $this.$store.dispatch('alerta',data)
            }
          )
      }
    },
    onReset() {
      // Reset our form values
      this.form.nombre = "";
      this.form.descripcion = "";
      if(this.edit){
        this.edit = false
        this.$emit('resetVal',true)
      
      }
    }
  },
  watch: {
      notaEdit: function(value){
          if(typeof value._id != 'undefined'){
              console.log('hola')
              this.edit = true
              this.form.nombre = value.nombre
              this.form._id = value._id
              this.form.descripcion = value.descripcion
              this.form.activo = value.activo
          }else{
              this.edit = false
              this.onReset()
          }
      }
  },
};
</script>

<style scoped>
#forlmulario-nota {
  flex: 2;
}
</style>
