<template>
  <div id="list-container">
    <!-- Alerta -->
    <transition name="zoom">
      <div v-if="notificacion.status" class="alert alert-dismissible" :class="'alert-'+notificacion.color" role="alert">
        <strong>{{notificacion.msg}}</strong>
        <button type="button" class="close" data-dismiss="alert" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>    
    </transition>
    <!-- {END} Alerta -->

    <!-- Table -->

    <b-card role="tablist" title="Listado de notas">
      <b-card no-body class="mb-1" v-for="(nota, index) in notas" :key="index">
        <!-- Header -->
        <b-card-header header-tag="header" class="p-1" role="tab">
          <b-button block href="#" v-b-toggle="'nota-'+index" class="text-capitalize text-left" size="sm" variant="outline-info">Nota: {{nota.nombre}}</b-button>
        </b-card-header>
        <!-- {END} Header -->

        <!-- Collapse -->
        <b-collapse :id="'nota-' + index" visible accordion="my-accordion" role="tabpanel">
          <b-card-body class="d-flex">
              <div class="w-50 border-right text-left">
                  <h4>Detalles</h4>
                  <p>Descripcion: {{nota.descripcion}}</p>
                  <p>Fecha: {{nota.date}}</p>
                  <p>Activo: <span class="badge badge-pill" :class="nota.activo?'badge-success':'badge-danger'">{{nota.activo?'Si':'No'}}</span></p>
              </div>
              <div class="w-50 pl-3">
                  <h4>Acciones</h4>
                  <b-button block href="#" variant="primary" @click="$emit('nota-edit',nota)">Editar</b-button>
                  <b-button block href="#" variant="outline-danger" @click="remove(nota._id)">Eliminar</b-button>
              </div>
          </b-card-body>
        </b-collapse>
        <!-- {END} Collapse -->

      </b-card>
    </b-card>
  </div>
</template>

<script>
import {mapActions} from 'vuex';
import {mapState} from 'vuex';

export default {
  created() {
    this.$store.dispatch("getNotas").then(
      res => {
        // this.showAlert();
      },
      err => {
        this.alerta({'msg': 'Error buscando las notas','color': 'danger'});
      }
    );
  },
  methods: {
    ...mapActions(['remove','alerta'])
  },
  computed: {
    ...mapState(['notas','notificacion','dismissCountDown'])
  }
};
</script>

<style scoped>
#list-container {
  flex: 3;
}
</style>